

import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import Chart from 'chart.js/auto';

const WeatherGraph = () => {
  const [weatherData, setWeatherData] = useState(null);
  const chartRef = useRef(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get('https://api.openweathermap.org/data/2.5/weather?q=London&appid=bd0fa5718ddca05faa2f31bbda199dfa&units=metric');
        setWeatherData(response.data);
      } catch (error) {
        console.error('Error fetching weather data:', error);
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    if (weatherData) {
      renderGraph();
    }
  }, [weatherData]);

  const renderGraph = () => {
    if (chartRef.current !== null) {
      chartRef.current.destroy();
    }

    const ctx = document.getElementById('weatherGraph').getContext('2d');

    const labels = ['Temperature', 'Humidity', 'Wind Speed'];
    const data = [
      weatherData.main.temp,
      weatherData.main.humidity,
      weatherData.wind.speed
    ];

    chartRef.current = new Chart(ctx, {
      type: 'bar',
      data: {
        labels: labels,
        datasets: [{
          label: 'Weather Data',
          data: data,
          backgroundColor: [
            'rgba(255, 99, 132, 0.2)',
            'rgba(54, 162, 235, 0.2)',
            'rgba(255, 206, 86, 0.2)'
          ],
          borderColor: [
            'rgba(255, 99, 132, 1)',
            'rgba(54, 162, 235, 1)',
            'rgba(255, 206, 86, 1)'
          ],
          borderWidth: 1
        }]
      },
      options: {
        scales: {
          y: {
            beginAtZero: true
          }
        }
      }
    });
  };

  return (
    <div>
      <h2 style={{textAlign:'center'}}>Previous Weather Details Graph</h2>
      <div style={{width:'100%', display:'flex', justifyContent:'center'}}>
        <div style={{width:'50%'}}><canvas id="weatherGraph"></canvas></div>
        </div>
    </div>
  );
};

export default WeatherGraph;
